import { Dialog, Toast } from 'vant'
import { getmid } from '@/utils/song'

const state = {
  listShow: false,
  playerShow: false,
  isPlay: false,
  songList: [],
  playerList: [],
  currentIndex: -1,
  loading: true,
  player: new QMplayer({
    onplay() {
      state.loading = false
    },
    onended() {
      state.currentIndex++
    }
  }),
}
const mutations = {
  showListState(state) {
    state.listShow = !state.listShow
  },
  playerShowState(state, val=true) {
    // Toast('歌词开发中')
    state.playerShow = val
  },
  songList(state, list) {
    state.songList = getmid(list)
  },
  playerList(state, list) {
    state.playerList = list
  },
  liked(state, idx) {
    let one = state.playerList[idx]
    one.liked = !one.liked
    state.playerList.splice(idx, 1, one)
  },
  addSongList(state, list) {
    state.songList.push(...getmid(list))
    state.playerList = state.songList
  },
  removeSongList(state, idx) {
    if (state.currentIndex === idx) {
      state.player.pause()
    }
    state.songList.splice(idx, 1)
    state.player.playNext()
  },
  currentIndex(state, val) {
    state.currentIndex = val
  },
  toggle(state) {
    state.isPlay = !state.isPlay
    state.player.toggle()
  },
}
const getters = {
  songIdList(state) {
    return state.playerList.map(e => e.mid)
  },
  getInfo(state) {
    return state.playerList[state.currentIndex]
  },
}
const actions = {
  play({ state, getters }, idx=0) {
    if (idx === state.currentIndex) return
    state.playerList = state.songList
    state.currentIndex = idx
    state.player.play(getters.songIdList, {
      index: state.currentIndex
    })
    state.loading = true
    state.isPlay = true
    let timer = setTimeout(_=> {
      if (state.loading) {
        Dialog.alert({
          message: '加载失败！',
        }).then(_=> {
          state.currentIndex = -1
        })
      }
      clearTimeout(timer)
    }, 5000)
  },
}
export default {
  namespaced: true,
  actions,
  state,
  getters,
  mutations,
}


