<template>
  <transition name="player" @after-enter="enter" @leave="leave">
    <div class="player" v-show="playerShow">
      <img class="bg" :src="getPic" alt="">
      <div v-if="getInfo" class="title">
        <van-icon name="arrow-left" @click="playerShowState(false)"/>
        <h2>{{getInfo.name}}</h2>
        <p>{{getInfo.singer}}</p>
      </div>
      <div class="cont">
        <transition name="photo" @after-enter="enter2" @leave="leave2">
          <div v-show="isShow">
            <img class="pane" :src="getPic" alt="">
          </div>
        </transition>
        <!-- <scroll-view class="lyric">
          <ul>
            <li v-for="(e, i) in 40" :key="i">{{i}}</li>
          </ul>
        </scroll-view> -->
      </div>
      <transition name="bottom">
        <div class="bottom" v-show="isShow2">
          <div class="bar">
            <span>00:00</span>
            <van-slider v-model="value" :min="0" :max="100" @change="onChange" />
            <span>00:00</span>
          </div>
          <div class="opt">
            <van-icon name="arrow-left" />
            <van-icon name="play-circle-o" />
            <van-icon name="arrow" />
          </div>
        </div>      
      </transition>
    </div>
  </transition>
</template>
<script>
import ScrollView from '#/ScrollView'
import { mapMutations, mapState, mapGetters } from 'vuex'
export default {
  name: 'Player',
  components: {
    ScrollView,
  },
  data() {
    return {
      value: 0,
      isShow: false,
      isShow2: false,
    }
  },
  methods: {
    ...mapMutations('player', ['showListState', 'playerShowState']),
    onChange() {

    },
    enter() {
      this.isShow = true
    },
    leave() {
      this.isShow = false
    },
    enter2() {
      this.isShow2 = true
    },
    leave2() {
      this.isShow2 = false
    },
  },
  computed: {
    ...mapGetters('player', ['getInfo']),
    ...mapState('player', ['playerShow']),
    getPic() {
      return this.getInfo ? `https://y.gtimg.cn/music/photo_new/T002R500x500M000${this.getInfo.pid}.jpg` : ''
    }
  },
}
</script>
<style lang="scss" scoped>
.player {
  z-index: 2;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #222;
  .bg {
    z-index: -1;
    position: fixed;
    top: 0;
    display: block;
    width: 100vw;
    height: 100vh;
    opacity: .6;
    filter: blur(20px); }
  .title {
    overflow: hidden;
    height: 15vh;
    color: #fff;
    .van-icon-arrow-left {
      margin: .2rem; }
    h2 {
      margin-bottom: .2rem;
      font-size: 20px;
      text-align: center; }
    p {
      font-size: 16px;
      text-align: center; } }
  .cont {
    // overflow: hidden;
    height: 60vh;
    padding: 0 10%;
    div {
      position: relative;
      height: 80vw;
      .pane {
        width: 100%;
        height: 100%;
        display: block;
        box-sizing: border-box;
        border-radius: 50%;
        border: 10px solid hsla(0, 0%, 100%,.1);
        animation: mRotate 20s linear infinite; }
    }
    .lyric {
      text-align: center;
      font-size: 16px; }}
  .bottom {
    height: 25vh;
    padding: 10%;
    .bar {
      display: flex;
      align-items: center;
      .van-slider {
        flex: auto;
        margin: 0 .2rem;
      }
      span {
        color: #fff;
        font-size: .28rem;
      }
    }
    .opt {
      display: flex;
      justify-content: space-around;
      align-items: center;
      color: #fff;
      font-size: .8rem;
      padding-top: .5rem;
    } } }
@keyframes mRotate {
  from {
    transform: rotateZ(0); }
  to {
    transform: rotateZ(360deg); }
}
.photo-enter,
.photo-leave-to {
  transform: translate(-200px, 400px); 
  }
.photo-enter-active,
.photo-leave-active {
  transition: .3s ease; }
.photo-enter-to,
.photo-leave {
  transform: translate(0, 0);
}
.bottom-enter,
.bottom-leave-to  {
  transform: translateY(25vh); }
.bottom-enter-active,
.bottom-leave-active {
  transition: .5s ease; }
.bottom-enter-to,
.bottom-leave {
  transform: translateY(0); }

.player-leave {
  opacity: 1; }
.player-leave-active {
  transition: .3s ease; }
.player-leave-to {
  opacity: 0; }
</style>



